import Vue from "vue";
import Vuex from "vuex";
import Cookie from "./Cookie/index.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    msg: {
      log: ""
    },
    suc: {
      logout: ""
    },
    err: {
      log: "",
      illegal: "~~"
    },
    isRouterAlive: true
  },
  mutations: {
    reload() {
      this.isRouterAlive = false;
      Vue.nextTick(() => {
        console.log(this.isRouterAlive);
        this.isRouterAlive = true;
      });
    }
  },
  modules: {
    ckData: Cookie
  }
});
